<template>
  <div>
    <v-list-group
        sub-group
        no-action
        :value="true"
    >
      <template v-slot:activator>
        <v-list-item-title>Genres</v-list-item-title>
      </template>

      <v-list-item
          v-for="(genre, i) in genres"
          :key="i"
          link
          @click="postGenre(genre.text)"
      >
        <v-list-item-title v-text="genre.text"></v-list-item-title>
      </v-list-item>
    </v-list-group>
  </div>
</template>

<script>
import {mapGetters} from "vuex";

export default {
  name: 'ShopList',

  computed: {
    ...mapGetters(['genres']),
  },

  methods: {
    postGenre(sel_genre) {
      this.$http.post('/shop/genre', {genre: sel_genre}).then((response) => {
        this.$emit('update', [response.data.items, sel_genre])
      })
    }
  },
}

</script>
